.card{min-height:240px;max-height:440px;position:relative;box-sizing:border-box;padding:16px 24px 0 0;display:flex;flex-direction:column;z-index:3}@media screen and (max-width: 1250px){.card{max-height:600px}}@media screen and (max-width: 1024px){.card{padding:16px 16px 0 0;min-height:380px}}@media screen and (max-width: 766px){.card{min-height:unset}}.card.animated:before{content:"";width:0;height:2px;position:absolute;top:0;left:0;background-color:var(--dark-default);animation:cardTopLine 1s ease forwards}.card.animated::after{content:"";width:2px;height:0;position:absolute;top:0;right:0;background-color:var(--dark-default);animation:cardRightLine 1s ease-in 1s forwards}.card__title{font-family:"Circe";font-style:normal;font-weight:700;font-size:26px;line-height:34px;color:var(--dark-default);text-transform:uppercase;margin:0 0 16px}@media screen and (max-width: 766px){.card__title{font-size:21px;line-height:28px}}.card__list{font-family:"Lora";font-style:normal;font-weight:400;font-size:17px;line-height:24px;margin:0;padding:0 0 0 12px;list-style:"• "}@media screen and (max-width: 766px){.card__list{font-size:15px;line-height:24px}}