.lectioncard{box-sizing:border-box;padding:40px 24px 0 0;display:grid;grid-template-columns:repeat(2, 1fr);column-gap:24px}.lectioncard.lectioncard_v_yellow{border-top:2px solid var(--accent-default)}.lectioncard.lectioncard_v_blue{border-top:2px solid var(--primary-default)}@media screen and (max-width: 1100px){.lectioncard{grid-template-columns:1fr;row-gap:24px}}.lectioncard__topic{display:flex;flex-direction:column}.lectioncard__topic_hide{display:none}.lectioncard__tag{font-family:"Circe";font-style:normal;font-weight:700;font-size:19px;line-height:24px;margin:0 0 8px}@media screen and (max-width: 766px){.lectioncard__tag{font-size:20px;line-height:26px}}.lectioncard__title{font-family:"Circe";font-style:normal;font-weight:700;font-size:22px;line-height:30px;text-transform:uppercase;margin:0}@media screen and (max-width: 766px){.lectioncard__title{font-size:18px;line-height:26px}}.lectioncard__list{list-style:"•  ";margin:0 0 16px;padding:0 0 0 16px;font-family:"Circe";font-style:normal;font-weight:400;font-size:19px;line-height:24px}@media screen and (max-width: 766px){.lectioncard__list{font-size:20px;line-height:26px}}.lectioncard__lecturer{font-family:"Circe";font-style:normal;font-weight:400;font-size:19px;line-height:24px;margin:0}@media screen and (max-width: 766px){.lectioncard__lecturer{font-size:20px;line-height:26px}}